.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  max-width: 80%;
  padding: 20px;
  font-family: 'Poppins', sans-serif;
}

h1 {
  color: #749e00;
  font-size: 24px;
  margin: 20px 0px;
}

/* Search Form */

.app__searchForm {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 20px;
}

.app__input, .app__cuisineSelect {
  padding: 6px 16px;
  border: 2px solid #ddd;
  border-radius: 4px;
  margin-right: 10px;
  font-size: 15px;
  min-width: 200px;
  max-width: 100vw;
}

.app__submit {
  background-color: #153f0b;
  color: white;
  border: 2px solid #153f0b;
  padding:6px 16px;
  font-size: 15px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.app__submit:hover {
  background-color: #1e3d17;
}

.app__cuisineSelect {
  background-color: white;
  cursor: pointer;
  transition: border-color 0.3s ease-in-out;
}

.app__cuisineSelect:focus {
  border-color: #007BFF; 
  outline: none; 
}

/* Recipes */
.app__recipes {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px 0;
}

.app__recipes div {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  text-align: center;
  text-decoration: none;
}

.recipe__image{
  width: 250px;
  height: 250px;
  object-fit: cover;
  border-radius: 4px;
}

.recipe__name{
  max-width: 250px;
  text-align: center;
  margin: 12px 0px;
  font-size: 15px;
  color: #ccc;
  text-decoration: none;
}

/* Pagination */

.pagination-info, .pagination {
  margin-top: 20px;
  text-align: center;
  color: #ccc;
}

.pagination button {
  padding: 8px 12px;
  margin: 0 5px;
  border: none;
  background-color: #f8f8f8;
  cursor: pointer;
  transition: background-color 0.3s;
  border-radius: 4px;
}

.pagination button:hover {
  background-color: #e2e2e2;
}

.pagination button:disabled {
  color: #ccc;
  cursor: not-allowed;
}

/* Responsiveness */
@media (max-width: 768px) {
  .app__input, .app__cuisineSelect {
    width: calc(100% - 22px); 
  }

  .app__submit {
    width: 100%;
    margin-top: 10px; 
  }

  .app__searchForm {
    flex-direction: column;
  }
}

.recipe-detail-container {
  display: flex;
  margin-top: 20px;
  gap: 20px;
  align-items: flex-start; 
}

.recipe-image {
  flex: 1;
  min-width: 550px;
  min-height: 550px;
  object-fit: cover;
}

.recipe-image img {
  width: 100%; 
  height: auto;
  border-radius: 8px; }

.recipe-info {
  flex: 2;
  padding: 20px;
  border: 1px solid #ddd; 
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(104, 104, 104, 0.1); 
}

.recipe-info h1, .recipe-info h3 {
  color: #ffffff;
}

.recipe-info ul {
  padding: 0;
  color: #a0a0a0;
}

.recipe-info li {
  padding: 8px 0;
  color: #a0a0a0;
}

.recipe__instructions{
  color: #a0a0a0;
}

@media (max-width: 768px) {
  .recipe-detail-container {
    flex-direction: column;
  }

  .recipe-image {
    max-width: 100%;
  }
}
